@import "../assets/libs/normalize.css/normalize.css";
@import "../assets/libs/reset.css/reset.css";
@import url("https://fonts.googleapis.com/css2?family=Cormorant+SC&family=Montserrat:wght@300;400;500&display=swap");
body {
  height: 2000px;
}

.background-image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.page-wrapper {
  display: flex;
  flex-direction: column;
  margin: 0;
  min-height: 100vh;
}
.page-wrapper_disable-scrolling {
  overflow: hidden;
}
.page-wrapper_theme_dark {
  background-color: #05061C;
}
.page-wrapper__main {
  flex: 1 1 auto;
}

.container {
  margin: 0 auto;
  padding: 0 0.9375rem;
  max-width: 74.375rem;
}

.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  transition: height 200ms ease-out;
  width: 100%;
  height: clamp(6.25rem, 5.681947317vw + 5.1136105366rem, 9.375rem);
  background-color: #FFFFFF;
}
.header_sticky {
  transition: height 200ms ease-in;
  height: 6.25rem;
  box-shadow: 0 0.3125rem 0.625rem 0.125rem rgba(0, 0, 0, 0.2);
}
.header_sticky.header_theme_dark {
  box-shadow: 0 -0.125rem 0.625rem -0.3125rem rgba(255, 255, 255, 0.25) inset;
}
.header_theme_dark {
  background-color: #05061C;
}
.header__container {
  flex: 1 1 auto;
}
.header__layout {
  position: relative;
  z-index: 2;
  display: flex;
  align-items: flex-start;
}
@media (max-width: 1199.98px) {
  .header__layout {
    align-items: center;
  }
}
.header__logo-wrapper {
  padding: 0;
  padding-left: clamp(0rem, 27.5vw + -20.62465625rem, 3.4375rem);
}
.header__logo-link {
  display: block;
}
.header__nav-wrapper {
  flex: 1 1 auto;
  display: flex;
  justify-content: flex-end;
  padding: 1.0625rem 0 0 0;
}
@media (max-width: 74.99875em) {
  .header__nav-wrapper {
    display: none;
  }
}
.header__burger-wrapper {
  display: none;
}
@media (max-width: 1199.98px) {
  .header__burger-wrapper {
    display: block;
  }
}
.header__switch-wrapper {
  padding: 0.5rem 0.75rem 0 0;
}
@media (max-width: 1199.98px) {
  .header__switch-wrapper {
    flex: 1 1 auto;
    display: flex;
    justify-content: flex-end;
    padding: 0 0.625rem 0 0.625rem;
  }
}
.header__mobile-menu-wrapper {
  position: absolute;
  left: 0;
  top: 0;
  display: none;
}
@media (max-width: 1199.98px) {
  .header__mobile-menu-wrapper {
    display: block;
  }
}

.responsive-image {
  display: block;
  max-width: 100%;
  height: auto;
}

.nav_vertical .nav__list {
  flex-direction: column;
  align-items: flex-start;
}
.nav_vertical .nav__link {
  padding: 0.9375rem 0;
}
.nav_theme_dark .nav__link {
  color: #FFFFFF;
}
.nav_theme_dark .nav__link:hover {
  color: #E9CF66;
}
.nav__list {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none;
}
.nav__link {
  display: block;
  transition: all 200ms ease;
  padding: 0.3125em 0.9375em;
  font-family: Montserrat, sans-serif;
  font-size: 1rem;
  line-height: 1.25em;
  text-decoration: none;
  color: #000000;
}
.nav__link:hover {
  color: #22C3C3;
}

.switch {
  position: relative;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}
.switch::before {
  content: "";
  position: absolute;
  left: 0.3125rem;
  top: 0.875rem;
  border-radius: 4.75rem;
  width: 2.375rem;
  height: 1.25rem;
  background-color: #0A0B20;
}
.switch::after {
  content: "";
  position: absolute;
  left: 0.4789473684rem;
  top: 1.0414473684rem;
  display: block;
  transition: left 200ms ease;
  border-radius: 50%;
  width: 0.9171052632rem;
  height: 0.9171052632rem;
  background-color: #E9CF66;
}
.switch_turn_on::before {
  background-color: #FFFFFF;
}
.switch_turn_on::after {
  left: 1.6039473684rem;
  background-color: #22C3C3;
}

.burger {
  position: relative;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}
.burger_cross .burger__line {
  transition: top 100ms ease, transform 100ms ease 100ms;
}
.burger_cross .burger__line_number_1 {
  top: 1.375rem;
  transform: rotate(45deg);
}
.burger_cross .burger__line_number_2 {
  opacity: 0;
  transition: none;
}
.burger_cross .burger__line_number_3 {
  top: 1.375rem;
  transform: rotate(-45deg);
}
.burger_theme_dark .burger__line {
  background-color: #FFFFFF;
}
.burger__line {
  position: absolute;
  left: 0.375rem;
  transition: transform 100ms ease, top 100ms ease 100ms;
  border-radius: 8px;
  width: 2.25rem;
  height: 0.25rem;
  background-color: #000000;
}
.burger__line_number_1 {
  top: 0.875rem;
}
.burger__line_number_2 {
  top: 1.375rem;
  transition: opacity 0s ease 100ms;
}
.burger__line_number_3 {
  top: 1.875rem;
}

.mobile-menu {
  overflow: hidden;
  transition: height 200ms ease-out, padding-top 0ms linear 200ms;
  box-sizing: border-box;
  padding-top: 0;
  width: 100vw;
  height: 0;
  background-color: #FFFFFF;
}
.mobile-menu_open {
  transition: height 200ms ease-in;
  padding-top: clamp(6.25rem, 5.681947317vw + 5.1136105366rem, 9.375rem);
  height: 100vh;
}
.mobile-menu_theme_dark {
  background-color: #05061C;
}
.mobile-menu_theme_dark .mobile-menu__scroll-area_shadow {
  box-shadow: 0 0.1rem 1.25rem 0 rgba(255, 255, 255, 0.25);
}
.mobile-menu__scroll-area {
  overflow: auto;
  width: 100%;
  height: 100%;
}
.mobile-menu__scroll-area_shadow {
  box-shadow: 0 0.5rem 0.625rem -0.625rem rgba(0, 0, 0, 0.4) inset;
}

.logo_theme_dark .logo__image_dark {
  display: block;
}
.logo_theme_dark .logo__image_light {
  display: none;
}
.logo__image_dark {
  display: none;
}

.title {
  font-family: "Cormorant SC";
  font-size: clamp(2.875rem, 1.5909452488vw + 2.5568109502rem, 3.75rem);
  line-height: 1.2166666667em;
  color: #000000;
}
.title_theme_dark {
  color: #FFFFFF;
}

.button {
  box-sizing: border-box;
  border: none;
  border-radius: 0.8333333333em;
  padding: 0.6666666667em 1.5833333333em;
  background-color: #000000;
  cursor: pointer;
  font-family: Montserrat;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.2083333333em;
  text-align: center;
  color: #FFFFFF;
}
.button_link {
  display: inline-block;
  text-decoration: none;
}
.button_accent {
  transition: background-color 200ms ease-out;
  background-color: #22C3C3;
}
.button_accent:hover {
  transition: background-color 200ms ease-in;
  background-color: #24cccc;
}
.button_theme_dark {
  background-color: #D3BB59;
}

.statistic-block {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.statistic-block__number {
  font-family: Montserrat;
  font-weight: 500;
  font-size: 2.25rem;
  line-height: 1.2222222222em;
  color: #EEECD5;
}
.statistic-block__description {
  font-family: Cormorant SC;
  font-weight: 500;
  font-size: 1.5rem;
  line-height: 1.2083333333rem;
  text-align: center;
  color: #FFFFFF;
}
.statistic-block__description_bounded {
  max-width: 5.125em;
}

.first-screen {
  padding-top: 15rem;
}
@media (max-width: 61.99875em) {
  .first-screen {
    padding-top: clamp(7.8125rem, 51.3392857143vw + -16.8297154018rem, 15rem);
  }
}
.first-screen_theme_dark .first-screen__tagline {
  color: #FFFFFF;
}
.first-screen_theme_dark .first-screen__statistics {
  background-color: #3A3A3A;
}
.first-screen__wrapper {
  position: relative;
  padding: 4.125rem 0 4.625rem;
  padding-top: clamp(0rem, 7.5001704584vw + -1.5000340917rem, 4.125rem);
}
.first-screen__title {
  margin-bottom: 0.25em;
}
.first-screen__tagline {
  margin-bottom: 1.1538461538em;
  max-width: 15.9615384615em;
  font-family: Montserrat;
  font-weight: 300;
  font-size: clamp(1.125rem, 0.9091115707vw + 0.9431776859rem, 1.625rem);
  line-height: 120%;
  color: #000000;
}
.first-screen__button {
  margin-bottom: clamp(3.75rem, 52.8846153846vw + -29.0378004808rem, 10.625rem);
  font-size: clamp(1.25rem, 0.4545557854vw + 1.1590888429rem, 1.5rem);
}
@media (max-width: 35.99875em) {
  .first-screen__button {
    width: 100%;
  }
}
.first-screen__strip {
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 3.4375rem;
}
@media (max-width: 61.99875em) {
  .first-screen__strip {
    gap: clamp(0.9375rem, 17.8571428571vw + -7.6337053571rem, 3.4375rem);
    margin-bottom: 3.75rem;
  }
}
@media (max-width: 47.99875em) {
  .first-screen__strip {
    display: block;
  }
}
.first-screen__statistics {
  margin-left: -625rem;
  padding: 1.25rem 3.125rem 0 632rem;
  background-color: #5D7B87;
}
@media (max-width: 61.99875em) {
  .first-screen__statistics {
    padding-left: clamp(628.125rem, 27.6785714286vw + 614.8396316964rem, 632rem);
  }
}
@media (max-width: 47.99875em) {
  .first-screen__statistics {
    padding: 1.25rem 0 1.5rem;
    margin: 0 0 2.5rem;
  }
}
.first-screen__videos {
  display: grid;
  grid-template-columns: repeat(auto-fit, 15rem);
  gap: 1.5625rem;
}
@media (max-width: 61.99875em) {
  .first-screen__videos {
    gap: clamp(0.9375rem, 4.4642857143vw + -1.2053013393rem, 1.5625rem);
  }
}
@media (max-width: 47.99875em) {
  .first-screen__videos {
    grid-template: auto 1fr/1fr;
  }
}
.first-screen__big-image {
  position: absolute;
  z-index: -1;
  right: 0;
  top: -4.125rem;
  display: block;
  max-width: 100%;
  height: auto;
}
@media (max-width: 61.99875em) {
  .first-screen__big-image {
    position: unset;
    z-index: unset;
  }
}

.video-preview {
  position: relative;
  cursor: pointer;
}
.video-preview__play-button {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.popup {
  position: fixed;
  z-index: 11;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0.9375rem;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
}
.popup_close {
  display: none;
}
.popup__cross {
  position: absolute;
  top: 0.9375rem;
  right: 0.9375rem;
}
.popup__content_youtube-video {
  width: 100%;
  max-width: 43.2rem;
  height: clamp(16.875rem, 24.1082191169vw + 12.0533561766rem, 23.625rem);
  max-height: 23.625rem;
}

.cross {
  width: 3rem;
  height: 3rem;
  cursor: pointer;
}
.cross:hover .cross__line {
  transition: background-color 200ms ease-in;
  background-color: #FFFFFF;
}
.cross__line {
  position: absolute;
  left: 0.375rem;
  top: 1.375rem;
  transition: background-color 200ms ease-out;
  border-radius: 0.25rem;
  width: 2.25rem;
  height: 0.25rem;
  background-color: #858586;
}
.cross__line_1 {
  transform: rotate(45deg);
}
.cross__line_2 {
  transform: rotate(-45deg);
}

.play-button {
  box-sizing: border-box;
  border: 0.0625rem solid #FFFFFF;
  border-radius: 50%;
  width: 4.6875rem;
  height: 4.6875rem;
  background: rgba(255, 255, 255, 0.3);
  cursor: pointer;
}
.play-button__triangle {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-0.375rem, -50%);
  display: block;
  box-sizing: border-box;
  border-left: 1.0625rem solid #FFFFFF;
  border-top: 0.625rem solid transparent;
  border-bottom: 0.625rem solid transparent;
}