.header {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 10;
  display: flex;
  align-items: center;
  transition: height 200ms ease-out;
  width: 100%;
  @include linear-lock(rem(100px), rem($min-screen-width), rem(150px), rem($lg), height, rem);
  background-color: $bg-light;

  @import "./_sticky/header_sticky.scss";
  @import "./_theme/header_theme_dark.scss";

  @import "./__container/header__container.scss";
  @import "./__layout/header__layout.scss";
  @import "./__logo-wrapper/header__logo-wrapper.scss";
  @import "./__logo-link/header__logo-link.scss";
  @import "./__nav-wrapper/header__nav-wrapper.scss";
  @import "./__burger-wrapper/header__burger-wrapper.scss";
  @import "./__switch-wrapper/header__switch-wrapper.scss";
  @import "./__mobile-menu-wrapper/header__mobile-menu-wrapper.scss";
}






